@import "@/assets/global.scss";





















input {
  width: 100%;
  max-width: $max-width;
  height: 57px;
  padding: 16px 14px;
  box-sizing: border-box;
  border: 1px solid #CBCBCB;
}

div span.visibility_icon, div span.check_icon, div span.search-icon, div span.visibility_off_icon {
  position: absolute;
  top: 50%;
  right: 14px;
  transform: translate(0, -50%);
}

.input-outline-x {
  position: relative;
}

.input-label {
  position: absolute;
  left: 16px;
  top: 50%;
  transform: translate(0, -50%);
  pointer-events: none;
}

.input-control {
  padding-top: 24px !important;
  padding-bottom: 8px !important;
  outline-color: #CBCBCB;
}

.input-outline:placeholder-shown::placeholder {
  color: transparent;
}

.input-outline:not(:placeholder-shown) ~ .input-label,
.input-outline:focus ~ .input-label {
  transition-duration: 0.1s;
  transform: scale(0.75) translate(-20%, -26px);
}

input:focus {
  outline: unset;
}

label {
  @extend .font-14-light;
  @extend .font-color-6;
}

.input-outline-x > input {
  margin-top: -1px;
  border-radius: 0;
  appearance: none;
}

.input-outline-x:first-of-type > input {
  border-radius: 8px 8px 0 0;
  margin-top: 0;
}

.input-outline-x:last-of-type > input {
  border-radius: 0 0 8px 8px;
}

.input-outline-x:only-of-type > input {
  border-radius: 8px 8px 8px 8px;
}

.error {
  z-index: 2;

  > input {
    border-color: #E10101;
  }
}
