@import "@/assets/global.scss";






































.item-panel {
  width: 100%;
  padding: 18px 10px;
  margin: 10px 0;
  background: #FFFFFF;
  box-sizing: border-box;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  display: flex;
}

.txtContent {
  width: auto;
  margin-left: 8px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
}
