@import "@/assets/global.scss";















































































































































































.main-content {
  padding: 121px 16px 24px 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tab {
  width: 100%;
  background: #FFFFFF;
  position: fixed;
  top: 52px;
  z-index: 1;
  border-bottom: 1px solid #FAFAFA;

  > .option-panel {
    width: 100%;
    max-width: $max-width;
    padding: 13px 16px;
    margin: 0 auto;
    display: flex;
    position: relative;

    > .option {
      background: #FFFFFF;
      border: 1px solid #BDBDBD;
      box-sizing: border-box;
      border-radius: 24px;
      padding: 8px 12px 8px 18px;
      margin-right: 12px;
      display: flex;
      @extend .font-16-normal;
      @extend .font-color-6;

      > span {
        margin-right: 8px;
      }
    }

    > .selected {
      @extend .font-color-hint;
      border: 1px solid #FCA311;
    }
  }
}

.search-bar {
  width: 100%;
  background: #FFFFFF;
  display: flex;
  position: fixed;
  top: 52px;
  z-index: 1;
  border-bottom: 1px solid #FAFAFA;

  > div {
    position: relative;
    width: 100%;
    max-width: $max-width;
    padding: 13px 16px;
    margin: 0 auto;
  }
}

.list {
  position: absolute;
  top: 69px;
  background: #FFFFFF;
  width: 100%;
  max-height: 262px;
  overflow-y: auto;

  > div {
    width: 100%;
    max-width: $max-width;
    margin: 0 auto;

    > div {
      @extend .font-14-normal;
      @extend .font-color-8;
      text-align: left;
      padding: 10px 24px;
    }
  }
}

.fadeHeight-enter-active,
.fadeHeight-leave-active {
  transition: all 0.2s;
  max-height: 262px;
}
.fadeHeight-enter,
.fadeHeight-leave-to
{
  max-height: 0;
}

.slot-img {
  position: absolute;
  top: 50%;
  right: 28px;
  transform: translate(0, -50%);
}

input {
  @extend .font-14-normal;
  width: 100%;
  height: 42px;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid #CBCBCB;
  border-radius: 8px;
  text-align: left;
}
